.header {
  background-image: url(../images/abstract-background-with-red-lines\ 1.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 41px;
}
.logo {
  display: flex;
  align-items: center;
}

.logo__icon {
  margin-right: 8px;
}
.logo__text {
  font-size: 30px;
  line-height: 35px;
  color: #ffffff;
  display: none;
  @include tablet {
    display: block;
  }
}
.navigation__list {
  //   @extend %flex;
  display: flex;
  //   justify-content: center;
  align-items: center;

  //   min-width: 281px;
  list-style: none;
}

.navigation__item:not(:last-child) {
  margin-right: 40px;
}
.navigation__link {
  display: block;
  color: inherit;
  font-size: 12px;
  line-height: 14px;
  padding-bottom: 2px;
  text-transform: uppercase;
  //   transition: color $animation;
  //   @include font(14px, 500, 16px, 0.02em);
}

.navigation__link.current {
  cursor: pointer;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    display: block;
    width: 100%;
    height: 3px;
    border-radius: 1.5px;
    background-color: #ff001b;
  }
}
.navigation__btn {
  border: none;
  background-color: transparent;
  display: block;
  color: inherit;
  font-size: 12px;
  line-height: 14px;
  padding-bottom: 2px;
  text-transform: uppercase;
}

.form {
  width: 100%;
  position: relative;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 16px;
  border-bottom: 0.5px solid #ffffff;
  @include mobile {
    width: 336px;
    padding-bottom: 4px;
  }
  @include desktop {
    width: 394px;
    margin-bottom: 20px;
    padding-bottom: 4px;
  }
}
.form__query {
  width: 100%;
  border: none;
  background-color: transparent;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
}
.form__button {
  position: absolute;
  border: none;
  background-color: transparent;
  top: 0;
  right: 0;
}
.wrong {
  color: #ff001b;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  visibility: hidden;
}
.visuallyHidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.js-show {
  visibility: visible;
}
